#root {
  min-height: 100vh;
}

.App {
  min-height: 100vh;
  position: relative;
}

.container {
  max-width: 1400px;
  width: 100%;
  padding: 0 10px;
  margin: 0 auto;
}

.content {
  padding-bottom: 90px;
}

.flex {
  display: flex;

  &.align-center {
    align-items: center;
  }

  &.align-start {
    align-items: flex-start;
  }

  &.align-end {
    align-items: flex-end;
  }

  &.justify-center {
    justify-content: center;
  }

  &.justify-start {
    justify-content: flex-start;
  }

  &.justify-end {
    justify-content: flex-end;
  }

  &.justify-between {
    justify-content: space-between;
  }

  &.justify-around {
    justify-content: space-around;
  }

  &.flex-col {
    flex-direction: column;
  }

  &.flex-row {
    flex-direction: row;
  }

  &.flex-wrap {
    flex-wrap: wrap;
  }

  &.flex-no-wrap {
    flex-wrap: nowrap;
  }
}

a {
  text-decoration: none;
}

.link-btn {
  width: 225px;
  height: 50px;
  border: 2px solid #000;
  background: transparent;
  color: #000;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.1s ease;
  font-size: 18px;
  font-weight: 500;
}

.section-title {
  font-size: 55px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 10px;
}

.button {
  width: 200px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: $red;
  font-size: 22px;
  font-weight: 500;
  text-transform: uppercase;
  transition: all 0.1s ease;
  border-top-left-radius: 60px;
  border-top-right-radius: 60px;
  border-bottom-left-radius: 60px;
  border-bottom-right-radius: 60px;
  border: none;
  cursor: pointer;
  outline: none;
}

.page-header {
  height: 440px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.dark-bg {
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.header-title-holder {
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 100px;
}
